import { Component, OnInit } from '@angular/core';
//importar servicios desde services
import { ServicioService } from '../../../services/servicio.service';
//importar controles formulario angular
import { NgForm } from '@angular/forms';
import { from } from 'rxjs';
import { Productos } from 'src/app/models/tareas';
@Component({
  selector: 'app-servicios',
  templateUrl: './servicios.component.html',
  styleUrls: ['./servicios.component.css']
})
export class ServiciosComponent implements OnInit {

  constructor(public ServicioService: ServicioService) { }

  ngOnInit() {
    this.ServicioService.getProduct();
    this.resetForm();
  }

  onSubmit(productForm: NgForm){
    this.ServicioService.insertProductos(productForm.value);
    this.resetForm(productForm);
  }
  resetForm(productForm?: NgForm){
    if (productForm != null) {
      productForm.reset();
      this.ServicioService.selectproduct = new Productos();
    }
  }

}
